<template>
  <div class="yeardetial">
    <sHeader :name= "title"  />
    <van-collapse v-for="(item, index) in yearList" :key="index" v-model="item.active" accordion>
      <van-collapse-item  :title="item['date']" :name="1" >
          <div class="list_record" v-for="(childItem, childIndex) in item.child" :key="childIndex">
            <div>
              <span>{{ childItem['measureTimeDesc'] }}</span>
              <span>体重  {{ childItem['weight'] }}</span>
              <span>BMI  {{ childItem['bmi'] || 0 }}</span>
              <span>BFR {{ childItem['bfp']}}%</span>
            </div>
          </div>
      </van-collapse-item>
    </van-collapse>
  </div>
</template>

<script>
import {onMounted, reactive, toRefs} from "vue";
import sHeader from '@/components/SimpleHeader'
import { useRoute } from "vue-router";

export default {
  name: "YearDetialList",
  components:{
    sHeader
  },
  setup(){
    const router = useRoute()

    /**
     * 声明
     * @type {UnwrapNestedRefs<{list: []}>} 数据列表
     */
    const state = reactive({
      yearList:[],
      params: {},
      title:""
    })

    onMounted(()=>{
      const { params } = router
      const item = JSON.parse(params.data)
      state.title = params.date
      state.yearList = item
      console.log('进入年列表界面',state.yearList)
    })

    return {
      ...toRefs(state)
    }
  }
}
</script>

